export const getLaoCompanyCostBy = function (costBy, type = null) {
  switch (costBy) {
    case 'container':
      return '';
    case 'fix_cost':
      return '';
    case 'chartered':
      return '';
    case 'bag':
      if (type == 'company') { return ''; }
      return '';
    case 'infect':
      return '';
    case '32km':
      return '32';
    default: return costBy;
  }
};

